<template>
  <v-card
    class="_user-panel"
    :class="{ _active: player.turnOn, _ended: !player.live }"
  >
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title>{{ player.name }}</v-list-item-title>
        <v-list-item-subtitle>핸디({{ player.score }})</v-list-item-subtitle>
      </v-list-item-content>
      <v-list-item-avatar
        v-if="player.live && player.turnOn"
        :color="player.ballColor"
      ></v-list-item-avatar>
    </v-list-item>

    <v-card-text class="_current-score">
      <!-- <v-btn absolute dark left color="pink" height="25" @click="decreaseAdditionalScore(1)">
        <v-icon>mdi-minus</v-icon>
      </v-btn>-->
      <span>{{ player.live ? currentScore : "종료" }}</span>
      <!-- <v-btn absolute dark right color="pink" height="25" @click="increaseAdditionalScore(1)">
        <v-icon>mdi-plus</v-icon>
      </v-btn>-->
    </v-card-text>

    <v-card-actions>
      <v-row>
        <v-col cols="7">
          <div>
            <v-btn large color="pink" @click="increaseScore(1)">+1</v-btn>&nbsp;
            <v-btn large color="pink" @click="increaseScore(2)">+2</v-btn>
          </div>
          <!-- <div>
            <v-btn small outlined color="pink" @click="decreaseScore(1)">-1</v-btn>
            <v-btn small outlined color="pink" @click="decreaseScore(2)">-2</v-btn>
            <v-btn small outlined color="pink" @click="decreaseScore(3)">-3</v-btn>
          </div>-->
        </v-col>
        <v-col cols="5" style="font-size: 11pt;">
          <div>
            이닝 :
            <span>{{ player.hr }}</span> / 득점 :
            <span>{{ player.getScore }}</span>
          </div>
          <div>
            에버리지 :
            <span>
              {{ player.hr ? (player.getScore / player.hr).toFixed(2) : 0 }}
            </span>
          </div>
        </v-col>
      </v-row>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: "TempPlayer",
  props: {
    player: {
      type: Object,
      default: null
    }
  },
  data: () => {
    return {};
  },
  computed: {
    currentScore() {
      return this.player.getScore;
    }
  },
  methods: {
    increaseScore(score) {
      this.$emit("score", this.player.name, score);
    }
  }
};
</script>

<style scoped>
._timer-panel,
._control-panel {
  height: 50vh;
}

._user-panel {
  height: 50vh;
}
._current-score {
  position: relative;
  font-size: 35pt;
}
._active {
  background-color: mediumvioletred !important;
}
._ended {
  background-color: gray;
  color: yellow;
}
</style>
