<template>
  <v-app id="inspire">
    <v-container class="grey lighten-5 _container">
      <v-row class no-gutters>
        <v-col cols="5">
          <TempPlayer :player="players[0]" @score="onScore" />
        </v-col>
        <v-col cols="2">
          <v-card class="pa-2 _timer-panel" outlined tile>
            <div>타이머</div>
            <div
              style="font-size: 33pt;"
              :style="elapsedTime > 30 ? 'background-color: red;' : ''"
            >
              {{ toHHMMSS(elapsedTime) }}
            </div>
            <div v-if="elapsedTime > 30">
              <img style="width: 100%;" :src="getTimerImage()" />
            </div>
          </v-card>
        </v-col>
        <v-col cols="5">
          <TempPlayer :player="players[1]" @score="onScore" />
        </v-col>
      </v-row>
      <v-row class no-gutters>
        <v-col cols="5">
          <TempPlayer :player="players[2]" @score="onScore" />
        </v-col>
        <v-col cols="2">
          <v-card class="pa-2 _control-panel" outlined tile>
            <v-btn
              color="success"
              dark
              style="height:40%; margin: 5px 0 5px 0"
              @click="restartTimer"
              >타이머 초기화</v-btn
            >
            <v-btn
              color="success"
              dark
              style="height:40%; margin: 5px 0 5px 0"
              @click="nextTurn"
              >턴 넘기기</v-btn
            >
          </v-card>
        </v-col>
        <v-col cols="5">
          <TempPlayer :player="players[3]" @score="onScore" />
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
// @ is an alias to /src
import TempPlayer from "@/temp/TempPlayer.vue";

const getRandomInt = function(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min; // 최댓값은 제외, 최솟값은 포함
};

const shuffle = function(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
};
export default {
  name: "TempGame",
  components: {
    TempPlayer
  },
  data: () => {
    let players = [
      {
        live: true,
        turnOn: false,
        name: "이범희",
        score: 11,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "허석",
        score: 8,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "이상원",
        score: 6,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "임정인",
        score: 10,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      }
    ];
    shuffle(players);
    players[0].turnOn = true;
    players[0].ballColor = "yellow";
    return {
      imgs: [
        "https://t1.daumcdn.net/liveboard/officeN/99123494be924faab047c76d00ba9798.jpg",
        "http://mblogthumb2.phinf.naver.net/20140111_17/cksdid221_1389373245533zS30t_JPEG/1389372839332.jpg?type=w2",
        "http://img.etoday.co.kr/pto_db/2018/01/20180112101606_1174914_710_340.jpg",
        "https://img.insight.co.kr/static/2017/03/20/700/01QLPVDUPXWR336N1JHM.jpg",
        "http://mblogthumb2.phinf.naver.net/20150105_141/hi_lbs_1420448403729cIMDs_JPEG/%C1%A4%B4%D9%C0%B1_%C2%A9_%284%29.jpg?type=w2",
        "http://mblogthumb1.phinf.naver.net/20150105_88/hi_lbs_14204483670287GrP7_JPEG/%C1%A4%B4%D9%C0%B1_%C2%A9_%285%29.jpg?type=w2",
        "http://110.47.126.228/Exo/AppImg2/i19880031.png"
      ],
      elapsedTime: 0,
      timer: null,
      players: players
    };
  },
  methods: {
    getLivePlayers() {
      return this.players.filter(player => player.live);
    },
    onScore(name, score) {
      const players = this.getLivePlayers();
      const scoredPlayer = players.filter(player => {
        return player.name === name;
      })[0];

      if (!scoredPlayer.live || !scoredPlayer.turnOn) {
        alert(scoredPlayer.name + "님의 턴이 아닙니다.");
        return;
      }

      scoredPlayer.getScore += score;
      this.restartTimer();
    },
    nextTurn() {
      this.restartTimer();
      const players = this.getLivePlayers();
      const idx = players.findIndex(player => player.turnOn);
      const nextIdx = (idx + 1) % players.length;
      players[idx].turnOn = false;
      players[idx].hr += 1;
      players[nextIdx].turnOn = true;

      const preBallColor = players[idx].ballColor;
      const nextBallColor = preBallColor === "yellow" ? "white" : "yellow";
      players.forEach(player => (player.ballColor = preBallColor));
      players[nextIdx].ballColor = nextBallColor;

      if (players[idx].getScore >= players[idx].score) {
        players[idx].live = false;
      }
    },
    toHHMMSS(secs) {
      var secNum = parseInt(secs, 10);
      var hours = Math.floor(secNum / 3600);
      var minutes = Math.floor(secNum / 60) % 60;
      var seconds = secNum % 60;

      return [hours, minutes, seconds]
        .map(v => (v < 10 ? "0" + v : v))
        .filter((v, i) => v !== "00" || i > 0)
        .join(":");
    },
    getTimerImage() {
      const idx = getRandomInt(0, this.imgs.length);
      return this.imgs[idx];
    },
    initTimer() {
      clearInterval(this.timer);
      this.elapsedTime = 0;
    },
    startTimer() {
      this.timer = setInterval(() => {
        this.elapsedTime++;
      }, 1000);
    },
    restartTimer() {
      this.initTimer();
      this.startTimer();
    }
  }
};
</script>

<style scoped>
._container {
  margin: 0;
  padding: 0;
}
._timer-panel,
._control-panel {
  height: 50vh;
}

._user-panel {
  height: 50vh;
}
._current-score {
  position: relative;
  font-size: 40pt;
}
</style>
