<template>
  <div id="inspire">
    <div class="container">
      <div class="vertical-box">
        <div class="left-box">
          <NormalPlayer :player="players[0]" @score="onScore" />
        </div>
        <div class="center-box">
          <div class="timer-box">
            <countdown
              class="time-count"
              v-if="timer.countdownStatus !== 'end'"
              :time="timer.settedTime * 1000"
              :interval="1000"
              @end="onCountdownEnd"
              @progress="onCountdownProgress"
            >
              <div class="time-count-number" slot-scope="props">
                {{ props.seconds }}<span class="time-count-text">초</span>
              </div>
            </countdown>
            <div class="timer-button--wrap">
              <radial-progress-bar
                :diameter="150"
                :total-steps="timer.settedTime"
                :completed-steps="completedSteps"
                :animate-speed="1000"
                :stroke-width="5"
                :startColor="'#10c469'"
                :stopColor="'#5b69bc'"
                :inner-stroke-color="'#232831'"
                :timing-func="'linear'"
              >
              </radial-progress-bar>
            </div>
          </div>
          <v-card>
            <v-btn
              color="success"
              dark
              large
              style="height:100%"
              @click="nextTurn"
              >턴 넘기기</v-btn
            >
          </v-card>
        </div>
        <div class="right-box"></div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import NormalPlayer from "@/components/NormalPlayer.vue";
import Countdown from "@/components/Countdown.vue";
import RadialProgressBar from "@/components/RadialProgressBar.vue";

const shuffle = function(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
};
export default {
  name: "NormalGame",
  components: {
    NormalPlayer,
    Countdown,
    RadialProgressBar
  },
  data: () => {
    let players = [
      {
        live: true,
        turnOn: false,
        name: "이범희",
        score: 11,
        getScore: 0,
        avatarUrl: "",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "허석",
        score: 8,
        getScore: 0,
        avatarUrl: "",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "이상원",
        score: 6,
        getScore: 0,
        avatarUrl: "",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "임정인",
        score: 10,
        getScore: 0,
        avatarUrl: "",
        hr: 0
      }
    ];
    shuffle(players);
    players[0].turnOn = true;
    return {
      imgs: [
        "https://t1.daumcdn.net/liveboard/officeN/99123494be924faab047c76d00ba9798.jpg",
        "http://mblogthumb2.phinf.naver.net/20140111_17/cksdid221_1389373245533zS30t_JPEG/1389372839332.jpg?type=w2",
        "http://img.etoday.co.kr/pto_db/2018/01/20180112101606_1174914_710_340.jpg",
        "https://img.insight.co.kr/static/2017/03/20/700/01QLPVDUPXWR336N1JHM.jpg",
        "https://fimg2.pann.com/new/download.jsp?FileID=20940179",
        "http://mblogthumb2.phinf.naver.net/20150105_141/hi_lbs_1420448403729cIMDs_JPEG/%C1%A4%B4%D9%C0%B1_%C2%A9_%284%29.jpg?type=w2",
        "http://mblogthumb1.phinf.naver.net/20150105_88/hi_lbs_14204483670287GrP7_JPEG/%C1%A4%B4%D9%C0%B1_%C2%A9_%285%29.jpg?type=w2",
        "http://optimal.inven.co.kr/upload/2016/12/24/bbs/i16516512355.jpg",
        "http://110.47.126.228/Exo/AppImg2/i19880031.png"
      ],
      timer: {
        settedTime: 40,
        countdownNumber: 15,
        countdownStatus: "ready"
      },
      players: players,
      ballColor: "yellow",
      inning: 0
    };
  },
  computed: {
    completedSteps() {
      return this.timer.settedTime - this.timer.countdownNumber;
    },
    isCountdownReady() {
      return this.timer.countdownStatus === "ready";
    }
  },
  methods: {
    getLivePlayers() {
      return this.players.filter(player => player.live);
    },
    onScore(name, score) {
      const players = this.getLivePlayers();
      const scoredPlayer = players.filter(player => {
        return player.name === name;
      })[0];

      if (!scoredPlayer.live || !scoredPlayer.turnOn) {
        alert(scoredPlayer.name + "님의 턴이 아닙니다.");
        return;
      }

      scoredPlayer.getScore += score;
    },
    nextTurn() {
      this.initTimer();
      const players = this.getLivePlayers();
      const idx = players.findIndex(player => player.turnOn);
      const nextIdx = (idx + 1) % players.length;
      players[idx].turnOn = false;
      players[idx].hr += 1;
      players[nextIdx].turnOn = true;

      this.changeBallColor();

      if (players[idx].getScore >= players[idx].score) {
        players[idx].live = false;
      }
    },
    changeBallColor() {
      this.ballColor = this.ballColor === "yellow" ? "white" : "yellow";
    },
    onCountdownProgress(time) {
      this.timer.countdownNumber = time.seconds - 1 > 0 ? time.seconds - 1 : 0;
    },
    onCountdownEnd() {
      this.timer.countdownStatus = "end";
    },
    onClickTimer() {
      if (this.isCountdownReady) {
        this.timer.countdownStatus = "start";
      }
    },
    initTimer() {
      this.timer.countdownStatus = "end";
      this.$nextTick(() => {
        this.timer.countdownNumber = this.timer.settedTime;
        this.timer.countdownStatus = "ready";
      });
    },
    setInning() {
      this.inning += 1;
    }
  }
};
</script>

